import axiosAuth from '../axiosAuth'
export default {
  methods: {
    justLog (str) {
      console.log(str)
    },
    refreshToken () {
      console.log('in')
      const formData = {
        username: localStorage.getItem('loginCode'),
        refresh_token: localStorage.getItem('refreshToken'),
        time_available: 3600
      }
      return axiosAuth.post('/refresh_token', JSON.stringify(formData))
        .then(res => {
          console.log(res)
          if (parseInt(res.data.status) === 0) {
            return 'failed'
          } else {
            localStorage.setItem('jwt', res.data.jwt)
            return 'refreshed'
          }
        })
        .catch(error => {
          alert(error)
        })
    }
  }
}
