<template>
	<v-layout row>
		<v-flex xs12>
			<v-slide-y-transition>
				<v-subheader v-if="isSearchVisible">
					<v-text-field append-icon="search" type="text" name="search" placeholder="ค้นหา..."></v-text-field>
				</v-subheader>
			</v-slide-y-transition>
			<v-expansion-panel>
				<v-expansion-panel-content v-for="(project,i) in projects" :key="i">
					<div slot="header">{{project.itemName}}</div>
					<v-card>
						<v-list two-line>
							<template v-for="(subProject, index) in project.itemData">
								<router-link
									:key="subProject.pj_code"
									avatar
									ripple
									@click="toggle(index)"
									:to="'room'" 
									tag="v-list-tile"
								>
									<v-list-tile-content>
										<v-list-tile-title>{{ subProject.pj_name }}</v-list-tile-title>
										<v-list-tile-sub-title class="text--primary">{{ subProject.short_name }}</v-list-tile-sub-title>
										<!-- <v-list-tile-sub-title>{{ project.pro }}</v-list-tile-sub-title> -->
									</v-list-tile-content>
								</router-link>
								<v-divider
									v-if="index + 1 < project.itemData.length"
									:key="index"
								></v-divider>
							</template>
						</v-list>
					</v-card>
				</v-expansion-panel-content>
			</v-expansion-panel>
    </v-flex>
  </v-layout>
</template>

<script>
	import { mapActions } from 'vuex'
	import { mapGetters } from 'vuex'
	import db from '../../database'
	import axios from '../../axiosRoomInspection'
	
	export default {
		data() {
			return {
				// projects: [{
				// 		projectId: 1,
				// 		projectCode: '048011',
				// 		projectName: 'พระราม 3',
				// 		projectNickname: 'Lpark RS-RM3'
				// 	},
				// 	{
				// 		projectId: 2,
				// 		projectCode: '048012',
				// 		projectName: 'พระราม 4',
				// 		projectNickname: 'Lpark RS-RM4'
				// 	},
				// 	{
				// 		projectId: 3,
				// 		projectCode: '048013',
				// 		projectName: 'พระราม 5',
				// 		projectNickname: 'Lpark RS-RM5'
				// 	},
				// 	{
				// 		projectId: 4,
				// 		projectCode: '048014',
				// 		projectName: 'พระราม 6',
				// 		projectNickname: 'Lpark RS-RM6'
				// 	}
				// ],
				// itemList: [{
				// 		itemListId: 1,
				// 		itemName: 'คอนโด',
				// 		itemFilter: 'Condo'
				// 	},
				// 	{
				// 		itemListId: 2,
				// 		itemName: 'บ้านพักอาศัย',
				// 		itemFilter: ''
				// 	},
				// ]
			}
		},
		created() {
			this.changeTitle('เลือกโครงการ')
			this.changePreviousRoute('menu')
			// console.log('created',this.$store.state.appTitle);
		},
		mounted() {
			
			this.refreshToken()
			.then(res => { 	
				const options = {
						method: 'GET',
						headers: {
							'Authorization': localStorage.getItem('jwt')
						},
						url: '/getProject'
					}
				if (res === 'refreshed') {
					axios(options)
					.then(res => {
						const data = res.data.data
						// ตรวจว่า get ข้อมูลมาได้หรือไม่
						if (res.data.code == 1) {
							// clear ข้อมูลใน IndexedDB Table Project 
							db.projects.clear()
							.then(res => {
								// เพิ่มข้อมูลโครงการที่ได้จาก API เข้าไปใน IndexedDB Table Project 
								db.projects.bulkAdd(data)
								.then(
									// อ่านข้อมูลโครงการที่เพิ่มเข้ามา
									db.transaction("rw", db.projects, () => {
										const promise = db.projects.toArray()
										.catch('DataError', err => {
											console.log('DataError',err)
										})
										return promise
									})
									.then(res => {
										this.setProjects(res) //เพิ่มข้อมูลโครงการใน store.state
									})
								)
								.catch(db.BulkError, err => {
									console.error("project fail to insert " + e.failures.length + " records");
								})
							})
							.catch(err => {
								console.error('Error could not clear projects table: ',err)
							})
						} else {
							console.log(data);
						}
		
					})
					.catch(error => {
						console.error(error);
					})
				}
				
			})
			
		},
		computed: {
			isSearchVisible() {
				// console.log('computed',this.$store.getters.appTitle)
				return this.$store.getters.searchVisible
			},
			projects() {
				const projects = this.$store.getters['roomInspection/getProjects']
				var projectCondo = []
				var projectOther = []
				var projectAll = []
				projects.forEach(project => {
					if(project.pj_type === 'Condo'){
						projectCondo.push(project)
					} else {
						projectOther.push(project)
					}
				})
				projectAll = [
					{
						itemListId: 1,
						itemName: 'คอนโด',
						itemData: projectCondo
					},
					{
						itemListId: 2,
						itemName: 'บ้านพักอาศัย',
						itemData: projectOther
					}
				]
				return projectAll
			}
		},
		methods: {
			...mapActions({
				changeTitle: 'setTitle',
      			changePreviousRoute: 'setPreviousRoute'
				
			}),
			...mapActions('roomInspection',{
				setProjects: 'setProjects'
			})
		}
	}
</script>
