<template>
    <v-container fluid row class="pb-3 px-1">
        <v-layout v-resize="onResize" row wrap >
            <v-flex xs4 sm3 md2 v-for="list in lists" :key="list.code">
                <div class="text-xs-center">

                    <v-btn fab dark large color="primary" @click="$router.push(list.path)" v-if="isLargeButton" >
                        <v-icon dark>{{ list.icon }}</v-icon>
                    </v-btn>
                    <v-btn fab dark color="primary" @click="$router.push(list.path)" v-else>
                        <v-icon dark>{{ list.icon }}</v-icon>
                    </v-btn>
                    <p class="primary--text subheading">{{ list.name }}</p>
                </div>
            </v-flex>
        </v-layout>
    </v-container>
</template>

<script>
    import {mapActions} from 'vuex'
    import {mapGetters} from 'vuex' 
    export default {
        data() {
            return {
                windowSize: {
                    x: 0,
                    y: 0
                },
                isLargeButton: false,
                lists: [
                    {
                        code: 'room-inspection',
                        name: 'ระบบตรวจรับห้อง',
                        icon: 'build',
                        path: '/room-inspection/project'
                    }
                ]
            }
        },
        mounted () {
            this.onResize()
        },
        created() {
            this.changeTitle("Menu");
            this.changePreviousRoute("login");
            // console.log('created',this.$store.state.appTitle);
        },
        methods: {
            ...mapActions({
                changeTitle: "setTitle",
                changePreviousRoute: "setPreviousRoute"
            }),
            onResize () {
                this.windowSize = { x: window.innerWidth, y: window.innerHeight }
                if (this.windowSize.x >= 768)
                {
                    this.isLargeButton = true
                }else {
                    this.isLargeButton = false
                }
            }
        }
    }
</script>
